import React from 'react';
import { Layout } from 'antd';
import { Route, Link } from 'react-router-dom';
import Main from '../routes/main'
import About from '../routes/about'
import Work from '../routes/work'; 
import Wlog from '../routes/wlog'; 
import Contact from '../routes/contact';
import { Typography } from 'antd';
import 'whatwg-fetch';

const { Title } = Typography;
const { Footer, Sider } = Layout;

class SiderDemo extends React.Component {
  state = {
    collapsed: false,
    color : '#fafafa',
  };

  onCollapse = collapsed => {
    console.log(collapsed);
    this.setState({ collapsed });
  };
  onSelect = selected => {
    console.log(selected);
    if (this.state.color == '#fafafa') {
      this.setState({ color: '#ffec3d' });
    } else {
      this.setState({ color: '#fafafa' });
    }
  }

  componentDidMount(){
		fetch('http://127.0.0.1:3001/projects',{
			method: 'get',
			dataType: 'json',
			headers:{
			'Accept': 'application/json',
			'Content-Type': 'application/json'
		}
		})
		.then((response) => response.json())
		.then((responseData) => {
			this.setState({proejct_list: responseData});
		})
		.catch((error)=>{
			console.log('Error fetching project',error);
		});
	}

  render() {

    return (
      <Layout style={{ minHeight: '100vh'}}>
        <div style={{backgroundColor: '#002766', width: '30%'}}>
          <Sider collapsible collapsed={this.state.collapsed} onCollapse={this.onCollapse} style={{backgroundColor: '#002766'}}>
            <div className="logo" >
              <Link to="/">
                <Title style={{margin: "30%", color: '#fafafa' }} >W</Title>
              </Link>
            </div>
            <div className="menu" style={{ backgroundColor: '#002766', flex: 1, flexDirection: 'column', margin:'50%' }}>
              <Link to="/about" onClick={this.onSelect}>
                <h1 style={{margin : "5vh 0vh", color : this.state.color }}>ABOUT</h1>
              </Link>
              <Link to="/work">
                <h1 style={{margin : "5vh 0vh", color : this.state.color }}>WORK</h1>
              </Link>
              <Link to="/wlog">
                <h1 style={{margin : "5vh 0vh", color : this.state.color }}>Wlog</h1>
              </Link>
              <Link to="/contact">
                <h1 style={{margin : "5vh 0vh", color : this.state.color }}>CONTACT</h1>
              </Link>
            </div>
          </Sider>
        </div>

 <Layout className="site-layout">
            <div className="route"> 
              <Route exact path="/intro" component={Main} /> 
              <Route path="/about" component={About} /> 
              <Route path="/work" component={Work} /> 
              <Route path="/wlog" component={Wlog} /> 
              <Route path="/contact" component={Contact} /> 
            </div>
          {/* <Content style={{ margin: 20, height:'100%'}} >
            <div className="site-layout-background" style={{ padding: 24, minHeight: 360 }}>
              <MainProject style={{display: 'flex'}}></MainProject>
            </div>
          </Content> */}
          <Footer style={{ textAlign: 'center' }}>Ant Design Â©2018 Created by Ant UED</Footer>
        </Layout>
      </Layout>
    );
  }
}


export default SiderDemo;
