import React from 'react';
import 'whatwg-fetch';
import { Card } from 'antd';
import { Row} from 'antd';

const { Meta } = Card;

// Parent Component
class MainProject extends React.Component {
	constructor(){
		super(...arguments);
		this.state = {
			proejct_list:[]
		};
	}

	componentDidMount(){
		fetch('http://127.0.0.1:3001/projects',{
			method: 'get',
			dataType: 'json',
			headers:{
			'Accept': 'application/json',
			'Content-Type': 'application/json'
		}
		})
		.then((response) => response.json())
		.then((responseData) => {
			this.setState({proejct_list: responseData});
		})
		.catch((error)=>{
			console.log('Error fetching project',error);
		});
	}
	render() {
		let proejct_list = this.state.proejct_list.map( (projects) => {
			return <PrintProj
					name={projects.name}
                    id={projects.id}
                    img = {projects.imgage}
					description={projects.description}
					{...projects}/>
		});

		return (
			<div>
				{proejct_list}
			</div>
		);
	}
}

// Child Component
class PrintProj extends React.Component {
	render() {
		return (
            <Row>
                <Card span={8}
                    hoverable
                    style={{ width: 500, flex: 1, flexDirection: 'row', margin:'10px' }}
                    cover={<img alt={this.props.id} src={this.props.img} />}
                >
                    <Meta title={this.props.name} description={this.props.description}/>
                </Card>
            </Row>
		);
	}
}
export default MainProject;